// const MP_KEY = "3gfybs5hew10cniyxf2fbxxna";
const MP_KEY = "8hwb8rt5gdxyf8rtp8gdbsakc";



export default class SpecificAPI {
  static API_BASE_URL = API_BASE_URL;
  static MP_KEY = MP_KEY;

  static getMatterTag(spaceId) {
    return new Promise(function(resolve, reject) {
      var url = API_BASE_URL + "mattertags/" + spaceId;

      axios
        .get(url)
        .then(result => {
          var data = result.data;
          resolve(data);
        })
        .catch(error => {
          console.error(error);
          reject(error);
        });
    });
  }

  static addMatterTag(data) {
    return new Promise(function(resolve, reject) {
      var url = API_BASE_URL + "mattertags";

      axios
        .post(url,data)
        .then(result => {
          var data = result.data;
          resolve(data);
        })
        .catch(error => {
          console.error(error);
          reject(error);
        });
    });
  }

  static deleteMatterTag(tagId,spaceId){
    return new Promise(function(resolve, reject) {
      var url = API_BASE_URL + "mattertags/"+tagId+"/"+spaceId;

      axios
        .delete(url, {})
        .then(result => {
          var data = result.data;
          resolve(data);
        })
        .catch(error => {
          console.error(error);
          reject(error);
        });
    });
  }
}
