import { message } from "antd";

export function formatPhone(phone) {
  var username = phone;
  if (username == null) {
    return false;
  }
  if (username.length == 10) {
    username = "+251" + username.substring(1);
  } else if (username.length == 12) {
    username = "+" + username;
  } else if (username.length == 9) {
    username = "+251" + username;
  }
  return username;
}

export function errorParser(err) {
  if (err && err.response && err.response.data.errors) {
    message.error(err.response.data.errors[Object.keys(err.response.data.errors)[0]][0]);
  } else if (err && err.response && err.response.data.error) {
    message.error(err.response.data.error.message);
  } else if (err && err.response && err.response.data) {
    message.error(Object.keys(err.response.data)[0] + " : " + err.response.data[Object.keys(err.response.data)[0]][0]);
  } else {
    message.error("Error while sending request please try again...");
  }
}

export function popupImportFileSelector(callback) {
  const input = document.createElement("input");
  input.type = "file";
  let file;
  input.onchange = e => {
    file = e.target.files[0];
    if (callback != null) {
      callback(file);
    }
  };
  setTimeout(() => {
    input.click();
  }, 100);
}


export function changeRGBToHex(data) {
  var r = data.r * 255;
  var g = data.g * 255;
  var b = data.b * 255;

  return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
}

export function changeHexToRGB(value) {
  var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(value);
  var data = result
    ? {
        r: parseInt(result[1], 16) / 255,
        g: parseInt(result[2], 16) / 255,
        b: parseInt(result[3], 16) / 255
      }
    : { r: 0, g: 1, b: 0 };
  return data;
}